"use client";
import ScrambledText from "@/animations/ScrambledText";
import SplashCursor from "@/animations/SplashCursor";
import React, { Component } from "react";

export default class Hero extends Component {
  render() {
    return (
      <>
        {/* <SplashCursor /> */}
        <div className="w-full h-screen font-sans overflow-hidden text-white bg-black">
          <ScrambledText
            className="py-45 px-6"
            radius={30}
            duration={1}
            speed={0.5}
            scrambleChars=""
          >
            <h1 className="text-5xl leading-14">
              <span className="font-bold text-6xl">W</span>e Build Products
              <br />
              That <span className="font-bold">Scale </span>
              <br />
              <span className="font-bold">No Code Required.</span>
            </h1>
          </ScrambledText>
        </div>
      </>
    );
  }
}
